@import url("https://fonts.googleapis.com/css2?family=Sora:wght@100;200&family=Ubuntu:wght@300&display=swap");

body {
  margin: 0;
  font-family: "Sora", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}
.navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
}

li {
  float: left;
  list-style-type: none;

  font-size: 25px;
}
/* ******************* */

button {
  cursor: pointer;
  color: white;
  margin: auto 2px;
  width: 75px;
  height: 5vh;
  background-color: #423161;
  border-radius: 15px;
  border: 2px #423161 solid;
  margin-right: 50px;
}

.container1 {
  margin-right: auto;
  margin-left: auto;
  display: flex;
  height: 80vh;
  width: 80vw;
  flex-direction: row;
  color: white;
  border-radius: 30px;
  background-color: #252525;
  margin-bottom: 20px;
}
.poster2box{
  text-align: center;
  color: white;
  width: 100%;
}
.poster2 {
width: 100%;
height: 100%;
border-radius:0px 30px 30px 30px;
}
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mainposter1div1 div {
  cursor: pointer;
  border: 2px #423161 solid;
  padding: 2px 8px;
  border-radius: 15px 0px 15px 15px;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  width: auto;
  font-size: 50px;
  bottom: 0;
}

.mainposter1div1 div:hover {
  color: white;
  background: #423161;
}

.genre1 {
  margin: auto;
  display: flex;
  width: 80vw;
  height: 5vh;
  align-items: center;
  justify-content: center;
  color: white;
}
.item1 {
  cursor: pointer;
  border: 2px #423161 solid;
  margin-right: 30px;
  width: auto;
  height: auto;
  border-radius: 15px 0px 15px 15px;
  padding: 5px 10px;
  font-size: 20px;
}
.item1:hover {
  transition: all 0.5s ease;
  background-color: #423161;
}

input {
  margin: auto;
  width: 250px;
  height: 5vh;
  border: 2px #423161 solid;
  border-radius: 15px;
  margin-right: 10px;
  outline: transparent;
  padding: 5px 9px;
  color: white;
  background-color: #252525;
}

.container4 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 15px;
  margin-right: auto;
  margin-left: auto;
  height: auto;
  width: 80vw;
}
.container3mono {
  background-color: #252525;
  cursor: pointer;
  margin-left: 4px;
  display: flex;
  margin: 4px;
  width: 32%;
  border: 2px #423161 solid;
  height: 90%;
  color: white;
  border-radius: 0 30px 30px 30px;
  transition: all 0.5s ease;
}
.container3mono:hover {
  transform: scale(1.1);
}
.c3poster1 {
  width: 50%;
  height: 100%;
  overflow: hidden;
  border-radius: 0 30px 0 30px;
}

.c3poster2 {
  padding-bottom: 15px;
  height: 100%;
  width: 50%;
  padding-left: 10px;
  overflow: hidden;
  border-radius: 0 30px 0 0;
}
.c3poster2 h3 {
  font-size: 22px;
}
.c3poster2 div {
  list-style: none;
  text-decoration: none;
  font-size: 14px;
  margin-top: 8px;
  font-size: 17px;
}
.poster1div1 div {
  cursor: pointer;
  border: 2px #423161 solid;
  margin-top: 18px;
  margin-right: 8px;
  padding: 2px 8px;
  border-radius: 15px 0px 15px 15px;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  width: 100px;
}
.poster1div1 div:hover {
  color: white;
  background: #423161;
}
.submitform{
  display: flex;
  align-items: center;
  justify-content: center;
}

.videoplayer{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20vh;
  flex-direction: column;
}
.video{
  width: 100vw;
  height: 70vh;
}
